.item{
    border: 1px solid;
    background-color: #856957;
    width: calc(100% / 4);
    min-width: 150px;
    margin-top: 10px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
}
h2,p{
    color: #f1f1f1;  
}
.item img{
    width: 80%;
    height: 80%;
}

